.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-bg {height:300px; background-position: center; background-size:  cover;}

.loading {background:rgba(255,255,255,0.9); color:#555555; display:flex; justify-content: center; align-items:center; position:fixed; width:100%; height:100%; z-index:100; top:0; left:0;}

.start {transform:scale(0)}
.end {transform:scale(1); transition: all .5s;}

.opa0 {opacity:0; transform:scale(0);}
.opa1 {opacity:1; transform:scale(1); transition: all .5s;}

.tab-menu-wrap {display:flex; justify-content: space-between;}
.tab-menu-wrap a {flex:1; text-align:center; box-sizing:border-box; padding:20px; background:skyblue; color:grey;}
.tab-menu-wrap + * a {background:none;}
.tab-menu-wrap a.on {background:yellowgreen; color:black;}
.tab-menu-wrap a img {width:100%;}

*{margin:0; padding:0; box-sizing:border-box;}